import { ReactComponent as Heart } from "./assets/heart.svg";
import "./App.scss";
import { useEffect, useState } from "react";

function fillZero(v) {
  return `0${v}`.substr(-2);
}
function foo() {
  const startDate = new Date("2020-12-22T21:43:00");
  let s = Math.floor((Date.now() - startDate.getTime()) / 1000);
  const d = Math.floor(s / 60 / 60 / 24);
  console.log(d);
  s -= d * 60 * 60 * 24;
  const h = Math.floor(s / 60 / 60);
  s -= h * 60 * 60;
  const m = Math.floor(s / 60);
  s -= m * 60;
  return {
    day: d,
    time: `${fillZero(h)}h ${fillZero(m)}m ${fillZero(s)}s`,
  };
}

function App() {
  const [state, setState] = useState(foo());
  useEffect(() => {
    document.title = `${state.day} days and ${state.time} We Fall In Love <3`;
    if (window._interval) {
      clearInterval(window._interval);
    }
    window._interval = window.setInterval(() => {
      const v = foo();
      setState(v);
    }, 1000);
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <div className="text">{state.time}</div>
        <Heart className="logo" />
        <p className="time-label">{state.day} days</p>
        <div className="text">&#x2764;We Fall In Love&#x2764;</div>
      </header>
    </div>
  );
}

export default App;
